import moment from 'moment';
import { AdvClient } from '../Assets/AdvertisementAsset'
import { shootingInfomationMsg } from '../component/Main/Section/shootingToast';
import { getRadomIndex } from '../util/commonMethod';
import {CALL_NEW_ADV, EXPIRE_ADV, SET_ADV_LIST,SUCCESS_ADV} from '../_actions/type'

const initialState={
    quest : {},
    clients : {...AdvClient}
}

export default function(state=initialState,action){
    let newState = {...state};
    switch (action.type) {
        case SET_ADV_LIST:
            if(Object.keys(action.payload).length > 0){
                return action.payload;
            }else{
                return newState;
            }
        case SUCCESS_ADV : {
            const quest = action.payload;
            const {client,key} = quest;
            if(newState.clients[client].friendliness + 3 > 100){
                newState.clients[client].friendliness = 100;
            }else{
                newState.clients[client].friendliness = newState.clients[client].friendliness + 3;
            }
            //     
            newState.clients[client].completed = newState.clients[client].completed + 1;

            delete newState.quest[key];

            return newState
        }
        case CALL_NEW_ADV : {
            // 
            const {quest,clients} = newState;
            //   3 +  ( 10 ) +    ( 10 )
            const {time:currentTime,viewer} = action.payload;

            

            Object.keys(clients).map(key=>{
                const {friendliness} = clients[key];

                let _eventRate = (3 + (viewer/100 > 10 ? 10 : viewer/100) + (friendliness/10 > 10 ? 10 :friendliness/10))/100;

                if(_eventRate > Math.random()){ // 
                    const _newQuest = {};

                    const {product,name} = clients[key];
                    //    
                    const _advTargetIndex = getRadomIndex(0,(product.length-1));
                    // 
                    _newQuest['contentsKey'] = product[_advTargetIndex]; 
                    //    (3~5)
                    
                    _newQuest['rating'] = getRadomIndex(3,5);
                    //  ( 3  10)   
                    const _period = getRadomIndex(3,10);
                    const _currentMoment = moment(currentTime).set('second',0);
                    _newQuest['period'] = _currentMoment.add(_period,'day');
                    /**
                     *  
                     *   : 40
                     *   4 1.5, 5 3
                     *     
                     * 3 x2, 4 x1.8, 5 x1.4, 6 x1.2, 7 x1
                     *   3
                     * **/ 
                    let startReward = 40;
                    if(_newQuest['rating'] === 4) startReward *= 1.5;
                    else if(_newQuest['rating'] === 5) startReward *= 3;

                    if(_newQuest['period'] === 3) startReward *= 2;
                    else if(_newQuest['period'] === 4) startReward *= 1.8;
                    else if(_newQuest['period'] === 5) startReward *= 1.4;
                    else if(_newQuest['period'] === 6) startReward *= 1.2;

                    //  10  -> 1+ (((10 / 10) * 2 ) / 10)
                    startReward *=  1 + (((friendliness / 10) * 2 ) / 10)
                    _newQuest['reward'] = (startReward*10000);
                    //  
                    _newQuest['accept'] = false;
                    // 
                    _newQuest['client'] = key;
                    _newQuest['key'] = `${_newQuest['contentsKey']}_${moment(currentTime).valueOf()}`;

                    //   
                    shootingInfomationMsg(`[ ] ${name}`);
                    quest[_newQuest['key']] = _newQuest;
                }
            })
            
            return newState;
        }
        case EXPIRE_ADV:
            const {quest} = newState;
            const currentTime = action.payload;
            if(Object.keys(quest).length > 0){
                Object.keys(quest).map(key=>{
                    if(moment(quest[key].period) < moment(currentTime)){
                        delete quest[key];
                    }
                })
            }
            return newState;
    
        default: return state;
    }
}