import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import CustomModal from '../../../UI/Modal/CustomModal'
import { JSONKeyLength, JSONtoArray, momentToDateString, sortingDateForList, sortingForObjectList } from '../../../util/commonMethod';
import VideoContents from './Section/VideoContents';
import VideoDeleteModal from './Section/VideoDeleteModal';

const VideoManagerComponent = memo((props) => {
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const EDITOR_LIST = useSelector(state => state.editorList);

    const [dateSortDirection, setDateSortDirection] = useState(false); //true : 'asc' || false:'desc'
    const [ratingSortDirection, setRatingSortDirection] = useState(false); //true : 'asc' || false:'desc'
    const [ratingFilter, setRatingFilter] = useState(0)
    const [genreSort, setGenreSort] = useState('');
    const [searchTerm, setSearchTerm] = useState('');
    const [videoDeleteModalOpen, setVideoDeleteModalOpen] = useState(false);


    const [videoList, setVideoList] = useState([]);

    useEffect(() => {
        if (JSONKeyLength(youtubeVideo) > 0) {
            let newList = [...JSONtoArray(youtubeVideo)];
            //
            newList = newList.filter(item => item.name.indexOf(searchTerm) > -1);
            //
            if (ratingFilter > 0) newList = newList.filter(item => item.rating === ratingFilter);
            //
            newList = sortingDateForList(newList, dateSortDirection, 'date');
            setVideoList(newList)
            // 
            // 1. 
            // 2. 
            // 3. 
            const proceed = newList.filter(y=>y.progress > 0);
            proceed.sort((a,b)=>{
                if(a.progress > b.progress){
                    return -1;
                }else if(a.progress < b.progress){
                    return 1;
                }
            })
            const unProceed = newList.filter(y=>y.progress === 0)
            setVideoList([...proceed,...unProceed])
            
        } else {
            setVideoList([]);
        }
    }, [youtubeVideo])


    const dateSort = () => { // 
        setDateSortDirection(!dateSortDirection);
        setVideoList(sortingDateForList(videoList, !dateSortDirection, 'date'))
    }
    // const ratingSort = () => { // , 
    //     setRatingSortDirection(!ratingSortDirection);
    //     const newList = sortingForObjectList(JSONtoArray(youtubeVideo), !ratingSortDirection, 'rating')
    //     setVideoList(newList)
    // }
    // const genreChange = (e) => { //  
    //     setGenreSort(e.currentTarget.value)
    //     if (genreSort) {
    //         const newList = JSONtoArray(youtubeVideo).filter(item => item.category.indexOf(e.currentTarget.value) > -1)
    //         setVideoList(newList)
    //     }
    // }

    const onRatingFilterChange = (e) => { // 
        const ratingValue = Number(e.target.value);
        setRatingFilter(ratingValue);
        if (ratingValue === 0) return setVideoList(JSONtoArray(youtubeVideo));

        const newList = JSONtoArray(youtubeVideo).filter(item => item.rating === ratingValue);
        setVideoList(newList)
    }
    // const onSearchTermChange = (e) => { // 
    //     setSearchTerm(e.currentTarget.value);
    //     const newList = videoList.filter(item => item.name.indexOf(e.currentTarget.value) > -1);
    //     setVideoList(newList);
    // }
    const onVideoDeleteModalOpen = ()=>{
        setVideoDeleteModalOpen(true);
    }

    return (
        <CustomModal
            {...props}
            title='  '
            className={'video_manager_wrapper'}
        >
            <div className={'video_manager_body'}>
                <div className={'search_bar'}>
                    <div onClick={dateSort}>{dateSortDirection ? '' : ''}</div>
                    <div ></div>
                    <select value={ratingFilter} onChange={onRatingFilterChange}>
                        <option value={0}></option>
                        <option value={1}> 1</option>
                        <option value={2}> 2</option>
                        <option value={3}> 3</option>
                        <option value={4}> 4</option>
                        <option value={5}> 5</option>
                    </select>
                    {/* <span></span>
                    <select value={genreSort} onClick={e => e.stopPropagation()} onChange={genreChange}>
                        <option value={''}></option>
                        <option value={'game'}></option>
                        <option value={'justchatting'}></option>
                    </select> */}
                </div>
                {/* <div className={'search_bar'}>
                    <span> </span>
                    <input placeholder={'  '} value={searchTerm} onChange={onSearchTermChange} />
                </div> */}
                <div className={'video_list'}>
                    {videoList.length > 0
                        ?
                        videoList.map((item, idx) => {
                            const manager = EDITOR_LIST.find(edt => edt.key === item.editor);
                            return (
                                <VideoContents item={item} key={item.key} {...props} manager={manager} />
                            )
                        })
                        :
                        <div className={'hc vc'}>
                              .
                        </div>
                    }
                </div>
                {videoList.length > 0 &&
                <div className='video_delete_button' onClick={onVideoDeleteModalOpen}>
                      
                </div>
                }
            </div>
            <VideoDeleteModal visible={videoDeleteModalOpen} ModalHandler={setVideoDeleteModalOpen}/>
        </CustomModal>
    )
})

export default VideoManagerComponent
