import moment from 'moment';
import { Activities } from '../Assets/ActionAsset';
import { INIT_ACTIVITY, SET_ACTIVITY, SET_ACTIVITY_CONTENTS, SET_VIEWER_COUNT } from '../_actions/type'

const initialState = {
    activity: 'resting',
    broadcastContents:{}, //  
    editingContents:{}, // 
    selfDevelopContents:{}, // 
    contents: {},
    startTime: moment().valueOf(),
    contentsStartTime: 0,
    totalBroadcastTime: 0, // 
    currentViewer: 0, // 
    averageViewer: 150, // 
    maxViewer: 0, //  
    gameStartTime: moment().valueOf(), // 
    accumulateBroadcastTime : 0, // 
}

export default function (state = initialState, action) {

    switch (action.type) {
        case INIT_ACTIVITY:
            return { ...state, ...action.payload };
        case SET_ACTIVITY:
            
            if (action.payload.activity === Activities.broadcast) {
                return { ...state, ...action.payload , broadcastContents : action.payload.contents}; 
            } else if(action.payload.activity === Activities.edit){
                return { ...state, ...action.payload, editingContents : action.payload.contents, currentViewer: 0 }; //    
            } else if(action.payload.activity === Activities.self_develop){
                return { ...state, ...action.payload, selfDevelopContents:action.payload.contents, currentViewer: 0 }; //    
            }else{
                return {...state,...action.payload}
            }

        case SET_ACTIVITY_CONTENTS:

            if (state.activity === Activities.broadcast) {
                
                return { ...state, ...action.payload , broadcastContents : action.payload.contents}; 
            } else if(state.activity === Activities.edit){

                return { ...state, ...action.payload, editingContents : action.payload.contents }; //    

            } else if(state.activity === Activities.self_develop){

                return { ...state, ...action.payload, selfDevelopContents : action.payload.contents }; //    
            }else{
                return {...state,...action.payload}
            }

        case SET_VIEWER_COUNT:

            return { ...state, ...action.payload }

        default: return state;
    }

}