import moment from 'moment';
import React, { useState } from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import CustomButton from '../../../../UI/Button/CustomButton';
import { infoMessage } from '../../../../UI/Message/message';
import { setActivity } from '../../../../_actions/activity_action';
import { setAwakeTime, setSleep } from '../../../../_actions/sleep_action';
import { Activities } from '../../../../Assets/ActionAsset';

function SleepPanel(props) {

    const currentTime = useSelector(state => state.currentTime);
    const { awakeTime } = useSelector(state => state.sleep);
    const [meridiem, setMeridiem] = useState(1); // 1 :  / 2 : 
    const [hour, setHour] = useState(0); //
    const [minute, setMinute] = useState(0) // 
    const dispatch = useDispatch();
    const { AwakeAlarmSet, setAwakeAlarmSet } = props

    const setAlarm = (value) => {
        setAwakeAlarmSet(value);
    }

    const onSleepAction = () => {
        if (moment(currentTime).diff(awakeTime, 'hour') < 2) {
            return infoMessage('  2    .')
        }
        const body = {
            AwakeAlarmSet,
            hour,
            minute,
            meridiem
        }
        // 
        //  6 ~  9
        const percent = Math.random() * 100;
        let sleepHour = 6;
        if (percent < 25) {
            sleepHour = 6;
        } else if (percent < 50) {
            sleepHour = 7;
        } else if (percent < 75) {
            sleepHour = 8;
        } else {
            sleepHour = 9;
        }
        if (AwakeAlarmSet) { //  
            //...     

        } else { //  
            //     
            const nextAwakeTime = moment(currentTime).add(sleepHour, 'hour').valueOf()
            dispatch(setSleep(true)); //
            dispatch(setAwakeTime(nextAwakeTime)); // 
            dispatch(setActivity(Activities.sleep, {}, nextAwakeTime)); //  
            props.ModalHandler(false)
        }
    }

    return (
        <div style={{ width: '100%', wordBreak: 'keep-all' }}>
                  .<br />
                     .
            <br /><br />
            <span style={{ color: 'red' }}>  2   .</span>
            {/* <hr />
            <div>
                <CustomCheckBox value={AwakeAlarmSet} onChange={setAlarm}>
                     
                </CustomCheckBox>
            </div> */}
            {
                AwakeAlarmSet &&
                <>
                    <hr />
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', justifyItems: 'center' }}>
                        <span> / </span>
                        <select onClick={(e) => e.stopPropagation()} style={{ height: '100%' }} value={meridiem} onChange={(e) => setMeridiem(Number(e.currentTarget.value))}>
                            <option value={1}></option>
                            <option value={2}></option>
                        </select>
                    </div>
                    <hr />
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr 1fr', justifyItems: 'center', alignItems: 'center' }}>

                        <div>
                            <input style={{ width: '100%' }} type="number" value={hour} onChange={e => setHour(Number(e.currentTarget.value))} />
                        </div>
                        <span></span>
                        <div>
                            <input style={{ width: '100%' }} type="number" value={minute} onChange={e => setMinute(Number(e.currentTarget.value))} />
                        </div>
                        <span></span>
                    </div>
                </>
            }
            <hr />
            <div style={{ height: '8vh', display: 'flex', justifyContent: 'flex-end' }}>
                <CustomButton
                    onClick={onSleepAction}
                ></CustomButton>
            </div>

        </div>
    )
}

export default SleepPanel
