import moment from "moment";
import { Categories } from "../Assets/ActionAsset";
import { calcDateCoefficient } from "../util/calcAlgorithm";
import { ADD_UPLOAEDE_VIDEO, GET_VIDEO_LIST_REVENUE, GET_VIDEO_REVENUE, SET_UPLOADED_VIDEO_LIST, VIEW_COUNT_UP } from "../_actions/type";


const initialState = [

]

export default function (state = initialState, action) {
    let newList = [];
    switch (action.type) {
        case SET_UPLOADED_VIDEO_LIST:
            return action.payload

        case ADD_UPLOAEDE_VIDEO:
            return [action.payload, ...state];

        case GET_VIDEO_REVENUE:
            newList = [...state];
            newList[newList.indexOf(action.payload)].revenue = 0;
            return newList;

        case GET_VIDEO_LIST_REVENUE:
            newList = [...state];
            newList.map(item => item.revenue = 0);
            return newList;

        case VIEW_COUNT_UP:
            /**
            * 
            *     * 5 .
            *       
            *    50% .
            *   80% .
            * 
            * 
            * 1 = 0.5
            * 2 = 0.7
            * 3 = 1
            * 4 = 1.5
            * 5 = 2.7
            * 
            *    *   * 1+0.  *  1  (     +- ) * 
            *   1000 * 1+( /100)* 1.43 *(1+0.)
            *   501  * 1+( /100)* 1.23 *(1+0.)
            *   101  * 1+( /100)* 1.08 *(1+0.)
            *   100  * 1+( /100)* 0.96 *(1+0.)
            * 
            *   2     
            * 7   
            * 14    50%  
            *     20%  
            * **/
            const { newsList, currentTime, averageViewer } = action.payload
            //  ,   
            const _newsTag = new Object;
            newsList.map(news => {
                let _keys = Object.keys(news.effect);
                _keys.map(key => {
                    _newsTag[key] = _newsTag[key] ? _newsTag[key] : 1;
                    _newsTag[key] = _newsTag[key] * (1 + (news.effect[key] / 100))

                })
            });

            //  
            const newYtbList = [...state];
            //    
            newYtbList.map(_ytb => {
                const { tag, rating, majority, view, revenue, upload_date } = _ytb;
                // 
                let _ratingCoefficient = 0.5;
                switch (rating) {
                    case 1: _ratingCoefficient = 0.2; break;
                    case 2: _ratingCoefficient = 0.5; break;
                    case 3: _ratingCoefficient = 0.9; break;
                    case 4: _ratingCoefficient = 1.5; break;
                    case 5: _ratingCoefficient = 2.2; break;
                }
                //  
                let _tagCoefficient = 1;
                tag.map(item => {
                    if (_newsTag[item]) {
                        _tagCoefficient *= _newsTag[item];
                    }
                })
                //   *   * 1+0.  *  1  (     +- ) * 
                // let viewInc = majority * _ratingCoefficient * (1 + averageViewer / 1000) * _tagCoefficient;

                //2021-12-14 
                //  *  * 1. *  
                let viewInc = averageViewer * _ratingCoefficient * (1 + majority / 100) * _tagCoefficient;
                
                //  
                const _diffDate = moment(currentTime).diff(upload_date, 'day');

                let _totalCoefficient = 1 + (Math.sqrt(averageViewer) / 1000);
                if (_diffDate < 2) {
                    _totalCoefficient *= (1 + (rating / 10));
                }

                //    
                if (rating === 5) {
                    if (_diffDate < 2) _totalCoefficient *= 1.4;
                    else if(_diffDate < 7) _totalCoefficient *= 0.88;
                    else _totalCoefficient *= 0.9
                } else if (rating === 4) {
                    if (_diffDate < 2) _totalCoefficient *= 0.9;
                    else if(_diffDate < 7) _totalCoefficient *= 0.6;
                    else _totalCoefficient *= 0.3;
                } else if (rating === 3) {
                    if (_diffDate < 2) _totalCoefficient *= 0.6;
                    else if(_diffDate < 7) _totalCoefficient *= 0.24;
                    else _totalCoefficient *= 0.12;
                } else if(rating === 2){
                    if (_diffDate < 2) _totalCoefficient *= 0.23;
                    else if(_diffDate < 7) _totalCoefficient *= 0.10;
                    else _totalCoefficient *= 0.08;
                } else if(rating === 1){
                    if (_diffDate < 2) _totalCoefficient *= 0.12;
                    else if(_diffDate < 7) _totalCoefficient *= 0.07;
                    else _totalCoefficient *= 0;
                }
                //   
                if (averageViewer > 1000) {
                    if (_diffDate < 2) _totalCoefficient *= 1.28;
                    else if(_diffDate < 7) _totalCoefficient *= 0.88;
                    else _totalCoefficient *= 0.14
                } else if (averageViewer > 501) {
                    if (_diffDate < 2) _totalCoefficient *= 1.08;
                    else if(_diffDate < 7) _totalCoefficient *= 0.7;
                    else _totalCoefficient *= 0.08;
                } else if (averageViewer > 101) {
                    if (_diffDate < 2) _totalCoefficient *= 1.05;
                    else if(_diffDate < 7) _totalCoefficient *= 0.62;
                    else _totalCoefficient *= 0.03;
                } else {
                    if (_diffDate < 2) _totalCoefficient *= 0.84;
                    else if(_diffDate < 7) _totalCoefficient *= 0.62;
                    else _totalCoefficient *= 0;
                }
                viewInc = viewInc * _totalCoefficient  * calcDateCoefficient(currentTime);
                //   ,    
                if(Categories.correction.view[_ytb.category]){
                    viewInc*= Categories?.correction?.view[_ytb.category];
                }

                const _newView = viewInc*(_ytb.advertisement ? 0.4 : 1); // , 40
                
                // 
                _ytb.view = view + _newView;

                // 
                if (_diffDate < 2) {
                    _ytb.revenue = revenue + (_newView * 0.7 * 1.1)
                } else if (_diffDate < 12) {
                    _ytb.revenue = revenue + (_newView * 0.3)
                } else {
                    _ytb.revenue = revenue + (_newView * 0.1)
                }
                //  
                if(Categories?.correction?.revenue[_ytb.category]){
                    _ytb.revenue *= Categories.correction.revenue[_ytb.category];
                }

                _ytb.view = Math.floor(_ytb.view);
                _ytb.revenue = Math.floor(_ytb.revenue);
                _ytb.total_revenue = Math.round(_ytb.total_revenue + Math.floor(_ytb.revenue));
            })
            return newYtbList;
        default:
            return state;
    }
}