import moment from 'moment';
import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import CustomButton from '../../../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../../../UI/Input/CheckBox/CustomCheckBox';
import { confirmMessage } from '../../../../../UI/Message/message';
import { AddComma, sortingForObjectList } from '../../../../../util/commonMethod';
import { SignInDeposit, SignInLoan } from '../../../../../_actions/asset_action';
import { getMoney, payMoney } from '../../../../../_actions/character_action';

const BankStatus = memo((props) => {


    const { deposit, loan } = useSelector(state => state.bank);
    const { deposit: assetDeposit, loan: assetLoan } = useSelector(state => state.asset);
    const currentTime = useSelector(state => state.currentTime);

    const dispatch = useDispatch();

    const [usingGoods, setusingGoods] = useState(false);
    const [depositList, setDepositList] = useState([]);
    const [loanList, setLoanList] = useState([]);

    const [depositInterestSortDirection, setDepositInterestSortDirection] = useState(false); //  
    const [depositMonthAmountSortDirection, setDepositMonthAmountSortDirection] = useState(false); //  

    const [loanInterestSortDirection, setLoanInterestSortDirection] = useState(false); //  
    const [loanMaxSortDirection, setLoanMaxSortDirection] = useState(false); //  


    useEffect(() => {
        setDepositList(deposit);
        setLoanList(loan);
    }, [loan])
    useEffect(() => {
        setDepositList(deposit);
    }, [deposit])


    const onChangeFilter = (flag) => { //  
        setusingGoods(flag);
        if (flag) {
            const newDepositList = deposit.filter(item => {
                const SignInCheck = assetDeposit.find(d => d.key === item.key);
                if (!SignInCheck) return item;
            })
            setDepositList(newDepositList)
            const newLoanList = loan.filter(item => {
                const SignInCheck = assetLoan.find(d => d.key === item.key);
                if (!SignInCheck) return item;
            })
            setLoanList(newLoanList)
        } else {
            setDepositList(deposit);
            setLoanList(loan);
        }
    }

    const depositInterestSort = () => { //  
        setDepositInterestSortDirection(!depositInterestSortDirection)
        const newList = sortingForObjectList(depositList, !depositInterestSortDirection, 'interest');
        setDepositList(newList);
    }
    const depositMonthAmountSort = () => { //  
        setDepositMonthAmountSortDirection(!depositMonthAmountSortDirection)
        const newList = sortingForObjectList(depositList, !depositMonthAmountSortDirection, 'month_amount');
        setDepositList(newList);
    }

    const loanInterestSort = () => { //  
        setLoanInterestSortDirection(!loanInterestSortDirection)
        const newList = sortingForObjectList(loanList, !loanInterestSortDirection, 'interest');
        setLoanList(newList);
    }
    const loanMaxSort = () => { //  
        setLoanMaxSortDirection(!loanMaxSortDirection)
        const newList = sortingForObjectList(loanList, !loanMaxSortDirection, 'max');
        setLoanList(newList);
    }

    const onSignInDeposit = (product) => {
        const { key, name, month_amount, expire, interest } = product;

        const expireMoment = expire ? moment(currentTime).add(expire.year, 'year').add(expire.month, 'months').add(expire.day, 'day').set('hour', 0).set('minute', 0).set('second', 0).valueOf() : null;

        confirmMessage(`<span className='strong'>${name}</span>  .${month_amount ? `<br/>   1(${AddComma(month_amount)}) .` : ''}`, () => {
            const body = {
                key: key,
                name: name,
                money: month_amount,
                month_amount,
                start: currentTime,
                interest,
                expire: expireMoment,
            }
            dispatch(SignInDeposit(body));
            dispatch(payMoney(month_amount));
        })
    }

    const onSignInLoan = (product) => { // 
        const { key, name, interest, max, expire } = product;
        const expireMoment = expire ? moment(currentTime).add(expire.year, 'year').add(expire.month, 'months').add(expire.day, 'day').set('hour', 0).set('minute', 0).set('second', 0).valueOf() : null;
        confirmMessage(`<span className='strong'>${name}</span>  .<br/>   .`, () => {
            const body = {
                key: key,
                name: name,
                interest,
                money: max + (max * interest),
                start: currentTime,
                expirt: expireMoment
            }
            dispatch(SignInLoan(body));
            dispatch(getMoney(max));
        })
    }


    return (
        <div className={'bank'}>
            <div>
                <CustomCheckBox value={usingGoods} onChange={onChangeFilter}>   </CustomCheckBox>
            </div>
            <div className={'title'}><span> </span></div>
            <div className={'list'}>
                <div className='list_contents header'>
                    <span></span>
                    <span></span>
                    <span onClick={depositInterestSort}>{depositInterestSortDirection ? '' : ''}</span>
                    <span onClick={depositMonthAmountSort}>{depositMonthAmountSortDirection ? '' : ''}</span>
                    <span></span>
                </div>
                {depositList.map(item => {
                    const SignInCheck = assetDeposit.find(d => d.key === item.key);
                    return (
                        <div className='list_contents' key={item.key}>
                            <span>{item.name}</span>
                            <span className={SignInCheck ? 'plus' : ''}>{SignInCheck ? ' ' : <CustomButton onClick={() => onSignInDeposit(item)}></CustomButton>}</span>
                            <span>{(item.interest * 100).toFixed(2)}%</span>
                            <span>{item.month_amount ? AddComma(item.month_amount) : ''}</span>
                            {item.expire ?
                                <span>
                                    {item.expire.year ? item.expire.year + '' : ''}
                                    {item.expire.month ? ` ${item.expire.month}` : ''}
                                    {item.expire.day ? ` ${item.expire.day}` : ''}
                                </span>
                                :
                                <span>N</span>
                            }
                        </div>
                    )
                })}
            </div>
            <div className={'title'}><span> </span></div>
            <div className={'list'}>
                <div className='list_contents header'>
                    <span></span>
                    <span></span>
                    <span onClick={loanMaxSort}> {loanMaxSortDirection ? '' : ''}</span>
                    <span onClick={loanInterestSort}> {loanInterestSortDirection ? '' : ''}</span>
                    {/* <span></span> */}
                </div>
                {loanList.map(item => {
                    const SignInCheck = assetLoan.find(d => d.key === item.key);
                    return (
                        <div className='list_contents' key={item.key}>
                            <span>{item.name}</span>
                            <span className={SignInCheck ? 'plus' : ''}>{SignInCheck ? ' ' : <CustomButton onClick={() => onSignInLoan(item)}></CustomButton>}</span>
                            <span>{item.max ? AddComma(item.max) : ' '}</span>
                            <span>{(item.interest * 100).toFixed(2)}%</span>
                            {/* {item.expire ?
                                <span>
                                    {item.expire.year ? item.expire.year + '' : ''}
                                    {item.expire.month ? ` ${item.expire.month}` : ''}
                                    {item.expire.day ? ` ${item.expire.day}` : ''}
                                </span>
                                :
                                <span>N</span>
                            } */}
                        </div>
                    )
                })}
            </div>
        </div>
    )
})

export default BankStatus
