import moment from "moment";
import React, { useRef } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { Activities } from "../../Assets/ActionAsset";
import CustomButton from "../../UI/Button/CustomButton";
import { confirmMessage } from "../../UI/Message/message";
import { ResetGame, setCurrentTime } from "../../_actions/currentTime_action";
import SettingModal from "../Menu/Setting/SettingModal";
import MainNoticeModal from "./MainNoticeModal";

function CharacterChoiceComponent(props) {
  const dispatch = useDispatch();
  const ContinueKey = useRef(window.localStorage.getItem("currentTime"));
  const [settingModalOpen, setSettingModalOpen] = useState(false);
  const [noticeModal, setNoticeModal] = useState(false);

  const sleep = JSON.parse(window.localStorage.getItem("sleep"));
  const activity = JSON.parse(window.localStorage.getItem("activity"));
  const character = JSON.parse(window.localStorage.getItem("character"));

  const [singleDetailMode, setSingleDetailMode] = useState(false);

  const onStartGame = () => {
    if (ContinueKey.current) {
      confirmMessage(
        "   .<br/> ?",
        () => {
          dispatch(ResetGame());
          props.history.push("/client/intro");
        }
      );
    } else {
      dispatch(ResetGame());
      props.history.push("/client/intro");
    }
  };

  const onContinueGame = () => {
    dispatch(setCurrentTime(moment().set("second", 0).valueOf()));
    props.history.push("/client/landing");
  };
  const onSettingModal = () => {
    setSettingModalOpen(true);
  };

  const onForceAwake = () => {
    confirmMessage(
      `+         .<br/>?`,
      () => {
        sleep.flag = false;
        sleep.faint = false;
        activity.activity = Activities.resting;
        window.localStorage.setItem("sleep", JSON.stringify(sleep));
        window.localStorage.setItem("activity", JSON.stringify(activity));
      }
    );
  };
  const onGotoEnding = () => {
    confirmMessage(
      `     .<br/>?<br/> `,
      () => {
        if (character.season) {
          character.season.value = 0;
        } else {
          const season = { text: "", value: 0 };
          character["season"] = season;
        }
        window.localStorage.setItem("character", JSON.stringify(character));
        props.history.push("/client/landing");
      }
    );
  };

  const onSelectMode = (_mode) => {
    if (_mode === "single") {
      setSingleDetailMode(true);
    } else {
      window.location.href = "https://streamer-tycoon2.vercel.app/";
    }
  };

  return (
    <div className={"character_choice"}>
      <div className={"box"}>
        <span className={"game_title"}>  </span>
        <span className={"game_sub_title"}>  V.2</span>
        {!singleDetailMode ? (
          <>
            <CustomButton
              style={{ backgroundColor: "#e2a64e" }}
              onClick={(e) => onSelectMode("multi")}
            >
              () 
            </CustomButton>
            <span> </span>
            <CustomButton onClick={(e) => onSelectMode("single")}>
               () 
            </CustomButton>
            <span> </span>
            <CustomButton type={"confirm"} onClick={() => setNoticeModal(true)}>
                
            </CustomButton>
          </>
        ) : (
          <>
            <CustomButton onClick={onStartGame}></CustomButton>
            {ContinueKey.current !== null && (
              <>
                <CustomButton onClick={onContinueGame}></CustomButton>
                <CustomButton type={"danger"} onClick={onGotoEnding}>
                  
                </CustomButton>
              </>
            )}
            <CustomButton onClick={onSettingModal}></CustomButton>
            <CustomButton
              style={{ backgroundColor: "#e2a64e" }}
              onClick={(e) => setSingleDetailMode(false)}
            >
               {" "}
            </CustomButton>

            {sleep?.flag === true && (
              <>
                <CustomButton type={"confirm"} onClick={onForceAwake}>
                   
                </CustomButton>
              </>
            )}

            <SettingModal
              visible={settingModalOpen}
              ModalHandler={setSettingModalOpen}
            />
          </>
        )}
        <MainNoticeModal visible={noticeModal} ModalHandler={setNoticeModal} />
      </div>
    </div>
  );
}

export default CharacterChoiceComponent;
