import React, { useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import CustomButton from '../../UI/Button/CustomButton';
import { confirmMessage } from '../../UI/Message/message';
import { setCharacterInfo } from '../../_actions/character_action';
import { setDifficuty } from '../../_actions/difficulty_action';

function CharacterStatusComponent(props) {

    const randomValue=()=>{
        let value = Math.random()*7
        value = value < 1 ? 1 : Math.round(value);
        return value;
    }

    const [name, setName] = useState("");
    const [face, setFace] = useState(randomValue());
    const [voice, setVoice] = useState(randomValue());
    const [speach, setSpeach] = useState(randomValue());
    const [health, setHealth] = useState(randomValue());
    const [physical, setPhysical] = useState(randomValue());
    const [personality, setPersonality] = useState(randomValue());
    const [edit, setEdit] = useState(5);

    const [diff, setDiff] = useState(1);

    const limit = useRef(5); //  

    const dispatch = useDispatch();

    const changeStatus = () => {
        setFace(randomValue())
        setVoice(randomValue())
        setSpeach(randomValue())
        setHealth(randomValue())
        setPhysical(randomValue())
        setPersonality(randomValue())
        setEdit(randomValue())
    }
    const onNext=()=>{
        confirmMessage(`  ?`,()=>{
            const body={
                name: {
                    text: ' ',
                    value: name,
                },
                face: {
                    text: '',
                    value: face,
                },
                voice: {
                    text: "",
                    value: voice
                },
                speach: {
                    text: '',
                    value: speach
                },
                health: {
                    text: '',
                    value: health
                },
                physical: {
                    text: ' ',
                    value: physical
                },
                personality: {
                    text: '',
                    value: personality
                },
                edit: {
                    text: '',
                    value: edit
                },
            }

            dispatch(setCharacterInfo(body));
            dispatch(setDifficuty(diff));
            props.history.push('/client/landing');
        })
    }

    return (
        <div className='character_status_wrapper'>
            <div className="body">
                <div className="row">
                     .
                </div>
                <div className="row">
                    <CustomButton className={'dice_btn'} onClick={changeStatus}>
                        <span> </span><i className="bi bi-shuffle"></i>
                    </CustomButton>
                </div>
                {/* <div className="row">
                       : 
                </div> */}
                <div className='row'>
                    <div className='title'>
                        
                    </div>
                    <input className='contents' type={'text'} value={name} onChange={e => setName(e.currentTarget.value)} />
                </div>
                <div className='row'>
                    <div className="title"></div>
                    <div className='contents'>{face}</div>
                </div>
                <div className='row'>
                    <div className="title"></div>
                    <div className='contents'>{voice}</div>
                </div>
                <div className='row'>
                    <div className="title"></div>
                    <div className='contents'>{speach}</div>
                </div>
                <div className='row'>
                    <div className="title"></div>
                    <div className='contents'>{health}</div>
                </div>
                <div className='row'>
                    <div className="title"></div>
                    <div className='contents'>{physical}</div>
                </div>
                <div className="row">
                    <div className="title"></div>
                    <div className='contents'>{personality}</div>
                </div>
                <div className="row">
                    <div className="title"></div>
                    <div className='contents'>{edit}</div>
                </div>
                <div className='row'>
                    <div className='title'></div>
                    <div className="contents">
                        <select value={diff} onChange={(e)=>setDiff(Number(e.target.value))}>
                            <option value={1}></option>
                            <option value={2}></option>
                            {/* <option value={3}></option> */}
                        </select>
                    </div>
                </div>
                <div className="row">
                    {diff === 1 && 
                    <>
                    <div className="title">
                        
                    </div>
                    <div className="contents">
                          : <br/>
                           
                    </div>
                    </>}
                    {diff === 2 && 
                    <>
                    <div className="title">
                        
                    </div>
                    <div className="contents">
                          :  , <br/>
                         ()  
                    </div>
                    </>}
                </div>
                <div className="row">
                    <CustomButton className='dice_btn' type={'danger'}
                    onClick={onNext}
                    ></CustomButton>
                </div>
            </div>

        </div>
    );
}

export default withRouter(CharacterStatusComponent);
