import moment from 'moment'
import React, { memo, useCallback, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CustomButton from '../../../UI/Button/CustomButton'
import { AddComma, JSONtoArray, momentTimeString, momentToTimerDateString } from '../../../util/commonMethod'
import { calcItemBoost, calcPassiveItemBoost, calcPhysiCoefficient, forceSleepTime, getDonationMoney, onChangeUploadViewCount, onViewerCountChange, stressDanger } from '../../../util/calcAlgorithm'
import { setActivity, setActivityContents, setViewerCount } from '../../../_actions/activity_action'
import { setCurrentTime } from '../../../_actions/currentTime_action'
import { DownSpeed, UpSpeed } from '../../../_actions/interval_action'
import { setAwakeTime, setFaint, setSleep } from '../../../_actions/sleep_action'
import { setStress, upStress } from '../../../_actions/stress_action'
import { donwTag, upTag } from '../../../_actions/tag_action'
import { useInterval } from './useInterval'
import { addVideo, upEditorProgress, upMyVideoProgress } from '../../../_actions/video_action'
import { faintTips } from '../../../util/tips';

import { downHealth, getMoney, payMoney, upHealth } from '../../../_actions/character_action'
import { shootingCommentMsg, shootingDonationMsg, shootingInfomationMsg, shootingYoutubeMsg } from './shootingToast'
import { getVideoListRevenue, setUploadVideoList, viewerCountUp } from '../../../_actions/uploaded_action'
import { changeCoinPrice } from '../../../_actions/coin_action'
import { setPlay } from '../../../_actions/play_action'
import { callNewsEvent, removeNews } from '../../../_actions/news_action'
import { checkMyCoin, depositInterestApply, terminateDeposit } from '../../../_actions/asset_action'
import { withRouter } from 'react-router';
import { setSideEffectItem, setUseItem } from '../../../_actions/inventory_action'
import { Activities, Categories } from '../../../Assets/ActionAsset'
import { callNewAdv, expireAdv } from '../../../_actions/adv_action'

const TimerComponent = memo((props) => {

    const { interval, speed } = useSelector(state => state.interval);
    const dispatch = useDispatch();
    const currentTime = useSelector(state => state.currentTime);
    const { activity, broadcastContents,editingContents,selfDevelopContents, startTime, contentsStartTime, currentViewer, averageViewer, maxViewer,contents } = useSelector(state => state.activity);
    const { health: { value: health }, voice, face, speach, personality, physical, edit, money: { value: myMoney } } = useSelector(state => state.character);
    const inventory = useSelector(state=>state.inventory);
    const tag = useSelector(state => state.tag);
    const { MIC, CAMERA, CHAIR, HOUSE } = useSelector(state => state.equipment);
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const editorList = useSelector(state => state.editorList);
    const uploadedList = useSelector(state => state.uploadedList);
    const newsList = useSelector(state => state.newsList);
    const play = useSelector(state => state.play);
    const {coinList} = useSelector(state => state.coin);
    const { deposit, loan, coin } = useSelector(state => state.asset)
    const { awakeTime } = useSelector(state => state.sleep);
    const stress = useSelector(state=>state.stress);
    const difficulty = useSelector(state=>state.difficulty);
    
    

    

    useEffect(() => {
        if (currentTime === 0) {
            props.history.push('/client/choice')
        }

    }, [currentTime])


    useInterval(() => {
        const newTime = moment(currentTime).add(1, 'minute');
        dispatch(setCurrentTime(newTime.valueOf()));
        
        //20 
        if (newTime.minute() % 10 === 0) {
            //   
            if (uploadedList.length > 0) {
                dispatch(viewerCountUp(newsList, newTime, averageViewer))
                const allMoney = uploadedList.reduce((acc, current) => {
                    return acc + current.revenue;
                }, 0)
                if (allMoney > 0) {
                    dispatch(getMoney(allMoney));
                    dispatch(getVideoListRevenue());
                }
            }
        }
        // 30 
        if (newTime.minute() % 30 === 0) {
            
            //  
            dispatch(upEditorProgress(newTime, JSONtoArray(youtubeVideo), editorList))
            
            //    
            if (newTime.diff(awakeTime, 'hour') > 22) {
                dispatch(upStress(8))
            } else if (newTime.diff(awakeTime, 'hour') > 18) {
                dispatch(upStress(5))
            }
        }
        if(difficulty === 1){ //    
            //   (4 )
            if (newTime.hour() % 4 === 0 && newTime.minutes() === 0) {
                //   
                dispatch(changeCoinPrice());
                //   
                const delistingCoin = coinList.filter(coin => coin.delisting);
                //      
                dispatch(checkMyCoin(delistingCoin));
            }
        }
        
        //    
        if (activity === Activities.broadcast) {
            //    5
            if (newTime.diff(startTime, 'minutes') % 5 === 0) {
                const viewrCount = onViewerCountChange(currentViewer, averageViewer, newTime.diff(startTime, 'minutes'), tag, broadcastContents.tag)
                dispatch(setViewerCount(viewrCount))
            }
            //  22
            if (newTime.diff(startTime, 'minutes') % 25 === 0) {
                const donation = getDonationMoney(currentViewer);
                if (donation > 0) {
                    if (donation > 100000) {
                        shootingCommentMsg(' <br/> <br/>~~! ~~~!')
                    } else if (donation > 50000) {
                        shootingCommentMsg(' .<br/>! !')
                    } else if (donation > 10000) {
                        shootingCommentMsg(' !!')
                    } else {
                        shootingCommentMsg('.')
                    }
                    shootingDonationMsg(`<span class='star'>${AddComma(donation)}</span><br/>`)
                    dispatch(getMoney(donation));
                }
            }
            if (newTime.diff(startTime, 'minutes') % 30 === 0) { // 30      
                //
                dispatch(downHealth());
                // 
                onStressUp(5.8);
            };
            //  30 
            if (newTime.diff(contentsStartTime, 'minutes') % 30 === 0) {
                // 
                const _tagItemList = calcPassiveItemBoost(inventory, 'tag');
                const _tagCurrection = _tagItemList.reduce((a, item) => { return a * item.value }, 1)
                dispatch(upTag(broadcastContents.tag, (0.05 * _tagCurrection)))

                if(contents.category === Categories.exercise){
                    //    
                    dispatch(upHealth(contents.advance));
                }
            }
            //1  
            if (newTime.diff(contentsStartTime, 'minutes') % 20 === 0) {
                const _itemList = calcPassiveItemBoost(inventory,'youtube')
                const _currectionList = {};
                if(_itemList.length > 0){
                    let _tiers = ['youtube','_5','_4','_3','_2','_1'];
                    //_currectionList
                    _tiers.map(t => {
                        _currectionList[t] = _itemList.reduce((a, item) => {
                            return a*item[t]
                        }, 1)
                    })
                }
                dispatch(addVideo(voice, speach, MIC, face, CAMERA, tag, broadcastContents, personality, currentTime, _currectionList, contents.category))
            }

        } else if (activity === Activities.edit) { // 
            if (newTime.diff(startTime, 'minutes') % 30 === 0) { // 30  
                dispatch(downHealth());//
                let myEditContents = JSONtoArray(youtubeVideo).find(ytb => ytb.editor === 'mine')
                if (myEditContents) {
                    if (myEditContents.progress === 100) {
                        //  
                        dispatch(setActivityContents({}, newTime))
                        shootingInfomationMsg(`${myEditContents.name} !`)
                    } else {
                        dispatch(upMyVideoProgress(edit, tag))
                    }
                    onStressUp(4.5);
                }
            };
        } else if (activity === Activities.resting) {// 
            if (newTime.diff(startTime, 'minutes') % 20 === 0) { // 20  
                onRecoverStress(physical.value);
            };
            if (newTime.diff(startTime, 'minutes') % 30 === 0) { // 30  
                dispatch(downHealth());
            };
            if (newTime.diff(startTime, 'minute') % 60 === 0) {
                //  
                dispatch(donwTag())
            }
        } else if (activity === Activities.sleep) { //  

            if (newTime.diff(startTime, 'minute') % 60 === 0) {
                //  
                dispatch(donwTag())
            }

        } else if (activity === Activities.exercise) { // 
            if (newTime.diff(startTime, 'minutes') % 30 === 0) {
                //10  1  1 0.1
                //  0.05 
                dispatch(upStress(5.2))
                dispatch(upHealth(0.05));
            }
        } else if (activity === Activities.self_develop) { //  
            
            //     0.08
            if (newTime.diff(contentsStartTime, 'minutes') % 15 === 0) {
                // 
                if(Object.keys(selfDevelopContents).length > 0){
                    const _tagItemList = calcPassiveItemBoost(inventory, 'tag');
                    const _tagCurrection = _tagItemList.reduce((a, item) => { return a * item.value }, 1)
                    dispatch(upTag(selfDevelopContents.tag,(0.04*_tagCurrection)));
                    onStressUp(3.1)
                }
            }
        }
        /************** *****************/
        if (newTime.hour() === 0 && newTime.minute() === 0) {
            // 
            dispatch(callNewsEvent(newTime));
            dispatch(removeNews(newTime));
            //    ()
            //   ()
            const expiredDeposit = deposit.filter(depo => {
                if ( moment(depo.expire).year() === moment(newTime).year() && moment(depo.expire).month() >= moment(newTime).month() && moment(depo.expire).date() >= moment(newTime).date()) {
                    dispatch(getMoney(depo.money));
                    shootingInfomationMsg(`(${AddComma(depo.money)}) .`, 'red')
                    return depo;
                }
            })
            if (expiredDeposit.length > 0) expiredDeposit.map(depo => dispatch(terminateDeposit(depo)))

            
        }

        /********** **************/
        // if (newTime.date() === 1 && newTime.hour() === 0 && newTime.minute() === 0) {
        if (newTime.day() === 1 && newTime.hour() === 0 && newTime.minute() === 0) {
            //    1 
            const editorPay = editorList.reduce((a, b) => { return a + b.cost }, 0);
            if (editorPay) {
                dispatch(payMoney(editorPay));
                shootingInfomationMsg(` (${AddComma(editorPay)}) .`, 'red')
            }

            //   ,   ||      
            // 1.  
            const depositInterestPay = deposit.reduce((a, b) => a + b.month_amount, 0);
            if (depositInterestPay > 0) {
                dispatch(payMoney(depositInterestPay)); // 
                dispatch(depositInterestApply()); //   
                shootingInfomationMsg(` (${AddComma(depositInterestPay)}) .`, 'red')
            }
            // 2.   =>   
            const loanInterestPay = loan.reduce((a, b) => a + (b.money * b.interest), 0);
            if (loanInterestPay > 0) {
                dispatch(payMoney(loanInterestPay));
                shootingInfomationMsg(` (${AddComma(loanInterestPay)}) .`, 'red')
            }

            /***  ****/
            dispatch(callNewAdv(newTime,averageViewer));
        }
        /************** ****************/
        if (newTime.date() === 1 && newTime.hour() === 0 && newTime.minute() === 0) {
            // 
            if (HOUSE.price > 0) {
                dispatch(payMoney(HOUSE.price));
                shootingInfomationMsg(`(${AddComma(HOUSE.price)}) .`, 'red')
            }
        }

        //  
        // if (myMoney < 0) {
        //     //  
        //     errorMessage(' .   .')
        //     setPlay(false);
        // }
        
        /*********** ****************/
        dispatch(expireAdv(newTime))

        /*******   ,  *********/
        if(Object.keys(inventory).length > 0){
            const keys = Object.keys(inventory);
            keys.map(key=>{
                // 
                if(inventory[key].use){
                    const {buffExpireTime,name,debuffExpireTime} = inventory[key]
                    if(buffExpireTime < newTime){ //   
                        //   
                        // 
                        
                        //      
                        const currentStressRatio = stress / (50 + calcItemBoost(inventory, 'stress') + (health * 10));
                        //  
                        dispatch(setUseItem(inventory[key].key , false));
                        //   ->   
                        let newStress = (50 + calcItemBoost(inventory, 'stress') + (health * 10)) * currentStressRatio
                        //  
                        newStress = newStress*(1.2-((physical.value-1)*2)/10)
                        dispatch(setStress(newStress))
                        setTimeout(() => {
                            if(debuffExpireTime > 0){
                                //   
                                //   
                                dispatch(setSideEffectItem(inventory[key].key,true));
                                let newStress = (50 + calcItemBoost(inventory, 'stress') + (health * 10)) * currentStressRatio
                                //  
                                newStress = newStress * (1.2 - ((physical.value - 1) * 2) / 10)

                                dispatch(setStress(newStress))
                                shootingInfomationMsg(`${name}   .`,'red');
                            }    
                        }, 500);
                    }else{
                        //   
                        const stressPassive = calcPassiveItemBoost(inventory, 'stress');
                        if(stressPassive.length>0){
                            stressPassive.map(passive=>{
                                const {interval,value} = passive;
                                if(newTime.minute()%interval === 0 ){
                                    dispatch(upStress(value))
                                }
                            })
                        }
                    }
                }else if(inventory[key].sideEffect){
                    const {debuffExpireTime} = inventory[key]
                    if(debuffExpireTime < newTime){
                        dispatch(setSideEffectItem(inventory[key].key,false))
                    }
                }
            })
        }
    }, (interval), play);

    const onPauseTimer = () => {
        dispatch(setPlay(!play))
    }
    const onFasterTimer = () => {
        dispatch(UpSpeed())
    }
    const onSlowerTimer = () => {
        dispatch(DownSpeed())
    }

    //(  )
    const onStressUp = (stressValue) => {
        let Coefficient = 1;
        switch (CHAIR.lvl) {
            case 2: Coefficient = 0.9; break;
            case 3: Coefficient = 0.85; break;
            case 4: Coefficient = 0.75; break;
            case 5: Coefficient = 0.6; break;
        }

        let physiCoefficient = calcPhysiCoefficient(physical.value);
        dispatch(upStress(stressValue * Coefficient * (physiCoefficient / 100)))
    }
    // 
    const onRecoverStress = (stressValue) => {
        let physiCoefficient = calcPhysiCoefficient(stressValue);
        const inc = ((100 - physiCoefficient) + 100) / 100;
        const _result = -(2 * inc)
        dispatch(upStress(_result))
    }


    return (
        <div className="Timer_box" id='Timer_box'>
            <div className="datetime">
                <div className="date">
                    {momentToTimerDateString(currentTime)}
                </div>
                <div className="time">
                    {momentTimeString(currentTime)}
                </div>
            </div>
            <div className="Timer_control_box">
                <CustomButton className="timer_btn">
                    {speed}x
                </CustomButton>
                <CustomButton className="timer_btn" onClick={onSlowerTimer}>
                    <i className="bi bi-skip-backward-fill"></i>
                </CustomButton>
                <CustomButton onClick={onPauseTimer} className="timer_btn">
                    {play ?
                        <i className="bi bi-stop-fill"></i>
                        :
                        <i className="bi bi-play-fill"></i>}
                </CustomButton>
                <CustomButton className="timer_btn" onClick={onFasterTimer}>
                    <i className="bi bi-skip-forward-fill"></i>
                </CustomButton>
            </div>
            
        </div>
    )
})

export default withRouter(TimerComponent);
