import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Categories } from '../../../../Assets/ActionAsset';
import { contents_product } from '../../../../Assets/shopAsset';
import CustomButton from '../../../../UI/Button/CustomButton';
import CustomModal from '../../../../UI/Modal/CustomModal'
import { AddComma, momentToDatetimeString } from '../../../../util/commonMethod';
import AdvSubmitModal from './AdvSubmitModal';


function AdvQuestDetailModal(props) {
    const {quest} = props;
    const {name:{value:myName}} = useSelector(state=>state.character);

    const [submitModalOpen, setSubmitModalOpen] = useState(false);

    useEffect(() => {
        
      
    }, [props.visible])
    const onAcceptQuest = ()=>{
        setSubmitModalOpen(true);
    }

    const renderingContents=()=>{
        if(quest?.client.category === Categories.game){
            return(
                <>
                      {quest?.client.name} {myName}   {quest?.contents.name}    .
                    <br />
                    <br />
                          ,       {myName}  
                     {quest?.client.name}     .
                    <br />
                    <br />
                    -  
                    <br />
                    <div className='title'>*  </div>
                    <div className="contents">{quest?.contents.name} </div>
                    <div className='title'>*  </div>
                    <div className="contents">
                        {momentToDatetimeString(quest?.quest.period)}
                    </div>
                </>
            )
        }else if(quest?.client.category === Categories.exercise){
            return(
                <>
                      {quest?.client.name} {myName}    {quest?.contents.name}    .
                    <br />
                    <br />
                       3 500  ,      {myName}  
                     {quest?.client.name}     .
                    <br />
                    <br />
                    -  
                    <br />
                    <div className='title'>*   </div>
                    <div className="contents">{quest?.contents.name} </div>
                    <div className='title'>*  </div>
                    <div className="contents">
                        {momentToDatetimeString(quest?.quest.period)}
                    </div>
                </>
            )
        }else{
            return(
                <>
                </>
            )
        }
    }
    
    return (
        <CustomModal 
        {...props}
        title={'  '}
        className={'quest_detail_modal'}
        >
            <div className='detail_script'>
                . {myName}
                <br/>
                <br/>
                {renderingContents()}
                <div className='title'>*  </div>
                <div className="contents"><span className="star"></span>{quest?.quest.rating}</div>
                <div className='title'>*  </div>
                <div className="contents">{AddComma(quest?.quest.reward)}</div>
                <br />
                <br />
                     . 
                <br />
                <br />
                .
                 .
                <br/>
                <br/>
                {quest?.client.name} 
            </div>
            <div className='detail_button'>
                <CustomButton type={'confirm'} onClick={onAcceptQuest}>
                     
                </CustomButton>
            </div>
            {quest?.contents ?
                <AdvSubmitModal quest={quest} visible={submitModalOpen} ModalHandler={setSubmitModalOpen} />
                :
                <></>
            }
        </CustomModal>
    )
}

export default AdvQuestDetailModal