import moment from "moment";

/**
 * @param health 
 * @returns flag true:  false: 
 * **/
export const stressDanger = (health) => {
    const faint = 75; //   
    const correction = health * 5 * 0.75; //  
    if ((Math.random() * 100) < (faint - correction)) {
        return true;
    } else {
        return false;
    }
}

