/**
 * 12~16  
 *    70% 
 * **/
export const forceSleepTime = () => {
    const percentage = Math.random() * 100;
    let addHour = 12;
    if (percentage < 20) {
        addHour = 12
    } else if (percentage < 40) {
        addHour = 13
    } else if (percentage < 60) {
        addHour = 14
    } else if (percentage < 80) {
        addHour = 15
    } else {
        addHour = 16
    }
    return addHour;
}


/**
 *    
 * **/
export const calcStressRecover = (sleepTime, health, stress = 0) => {
    // 9   10  92 
    let min = 38; //  

    if (sleepTime < 7) {
        min = 30 + (sleepTime * health * 1.1);
    } else if (sleepTime < 8) {
        min = 33 + (sleepTime * health * 1.15);
    } else if (sleepTime < 9) {
        min = 33 + (sleepTime * health * 1.2);
    } else {
        min = 33 + (sleepTime * health * 1.08);
    }
    if (stress > 90) {
        return min * 0.85;
    } else {
        return min;
    }


}
/**
 *    
 * **/
export const calcForceRecover = (stress,sleepTime, health) => {
    // 9   10  92 
    //12~16 
    let min = stress*0.25; //  

    if (sleepTime === 12) {
        min = (stress*0.3) + (sleepTime * health * 0.45);
    } else if (sleepTime === 13) {
        min = (stress*0.23) + (sleepTime * health * 0.48);
    } else if (sleepTime === 14) {
        min = (stress*0.22) + (sleepTime * health * 0.40);
    } else {
        min = (stress*0.25) + (sleepTime * health * 0.38);
    }
    return min;
}
/**
 *   
 * **/
export const calcVoiceCoefficient = (voice, speach, mic) => {
    let result = 1;

    if (voice > 9) { //     
        result = voice * (speach * 1.5)
    } else {
        if (speach > 8) {
            result = voice * speach
        } else if (speach > 6) {
            result = voice * (speach * 0.8);
        } else if (speach > 4) {
            result = voice * (speach * 0.5);
        } else {
            result = voice * (speach * 0.2)
        }
    }
    switch (mic) {
        case 1: result = result * 0.4; break;
        case 2: result = result * 0.7; break;
        case 3: result = result * 1; break;
        case 4: result = result * 1.3; break;
        case 5: result = result * 1.7; break;
    }

    return (result + 100) / 100
}

/**
 * ,  
 * **/
export const calcFaceCoefficient = (face, camera) => {
    let result = 15;
    if (face > 9) {
        result = result + (face * camera)
    } else if (face > 7) {
        result = result + ((face * camera) * 0.7)
    } else if (face > 5) {
        result = result + ((face * camera) * 0.5)
    } else if (face > 3) {
        result = result + ((face * camera) * 0.1)
    } else {
        result = result - ((face * camera) * 0.8)
    }
    result = result < 0 ? 0 : result;
    return (result + 100) / 100
}

/**
 *   
 * **/
export const calcTagCoefficient = (tagList = [], contentsTag = []) => {
    let result = 0;
    contentsTag.map(tag => {
        let myTag = tagList.find(t => t.name.toUpperCase() === tag);
        if (myTag) {
            result += myTag.value;
        }
    })

    let average;

    if (result === 0) {
        average = 0
    } else {
        average = (result / contentsTag.length) * 0.55
    }

    return (average + 100) / 100
}

/**
 *  
 * **/
export const calcPersonalityCoefficient = (value) => {
    //     .
    let result = 10

    if (value > 9) {
        result = result + (result * (value / 20))
    } else if (value > 5) {
        result = result + (result * (value / 30))
    } else if (value > 2) {
        result = result - (result * (value / 20))
    } else {
        result = 1
    }

    return (result + 100) / 100
}

/**
 *   
 * @param    (%)
 * @output   1~5
 * **/
export const calcYoutubeRating = (characterCurrection,itemCurrection) => {
    // 5  .
    let _ytb_1 = 25
    let _ytb_2 = 35
    let _ytb_3 = 25
    let _ytb_4 = 10
    let _ytb_5 = 5
    
    //       .
    let {_5=1,_4=1,_3=1,_2=1,_1=1} = itemCurrection;
    
    //    0.63     .
    _ytb_5 = (_ytb_5 + ((characterCurrection * 0.63) / _ytb_5))*_5; //5
    _ytb_4 = (_ytb_4 + ((characterCurrection * 0.77) / _ytb_4))*_4; // 4
    _ytb_3 = (_ytb_3 + ((characterCurrection) / 10))*_3; //3
    _ytb_2 = ((100 - _ytb_5 - _ytb_4 - _ytb_3) * 0.60)*_2;
    _ytb_1 = ((100 - _ytb_5 - _ytb_4 - _ytb_3) * 0.40)*_1;

    let randomPercentage = Math.random();

    if (randomPercentage < _ytb_5 / 100) {
        return 5
    } else if (randomPercentage < (_ytb_5 + _ytb_4) / 100) {
        return 4
    } else if (randomPercentage < (_ytb_5 + _ytb_4 + _ytb_3) / 100) {
        return 3
    } else if (randomPercentage < (_ytb_5 + _ytb_4 + _ytb_3 + _ytb_2) / 100) {
        return 2
    } else {
        return 1
    }
}

/**
 *     
 * **/
export const onEditYoutubevideo = (edit, tagList, contents) => {
    if (contents.progress === 100) {
        return contents
    } else {
        const tagCoefficient = calcTagCoefficient(tagList, contents.tag);
        let newProgress = contents.progress + (edit * 1.3 * tagCoefficient);
        const newContents = { ...contents };
        if (newProgress > 100) { //
            newProgress = 100
            newContents.progress = 100;
        } else {
            newContents.progress = Number(newProgress.toFixed(1));
        }

        return newContents;
    }
}
/**
 *  
 * @param1 
 * @param2  
 * @param3   
 * **/
export const onChangeEditorProgress = (currentTime, youtubeList = [], editorList = []) => {
    //  5  
    // 1   8 
    // 1   10 
    // 2  6 6
    // 3  4 4 4
    // 4  3 3 3  .
    const _ylist = JSON.parse(JSON.stringify(youtubeList)); //     JSON  
    const currentHour = moment(currentTime).hour();
    const newList = _ylist.map((ytb, idx) => {
        if (ytb.editor && ytb.editor !== 'mine' && ytb.progress < 100) {
            //  
            const { working_time, tag, edit_ability, key } = editorList.find(item => item.key === ytb.editor);
            //   
            if (currentHour >= working_time[0] && currentHour <= working_time[1]) {
                //    
                let matchingTagCount = 0;
                ytb.tag.map(t => {
                    if (tag.indexOf(t) > -1) {
                        matchingTagCount = matchingTagCount + 1;
                    }
                })
                // 30   
                const _workingRate = edit_ability * 1.18;
                //      
                const _matchRate = matchingTagCount > 0 ? ((matchingTagCount / ytb.tag.length) + 1) : 1;
                //    
                const _workingCount = youtubeList.filter(i => i.editor === key).length;
                //    
                let upRate = 0;
                if (_workingCount > 1) {
                    upRate = _workingRate * _matchRate;
                } else {
                    upRate = ((_workingRate / _workingCount) * 1.3) * _matchRate;
                }
                ytb.progress = Number((ytb.progress + upRate).toFixed(1));

                if (ytb.progress > 100) {
                    ytb.progress = 100;
                    //    20% *   
                    const _ratingUpWin = 10 * _matchRate * ((edit_ability / 25) + 1);
                    if (Math.random() < _ratingUpWin / 100) {
                        // 
                        //1   80% 2   15% 3   5%
                        const _ratingUpPercent = Math.random();
                        if (_ratingUpPercent < 0.05) {
                            ytb.rating = ytb.rating + 1;
                        } else if (_ratingUpPercent < 0.20) {
                            ytb.rating = ytb.rating + 2;
                        } else {
                            ytb.rating = ytb.rating + 3;
                        }
                        ytb.rating = ytb.rating > 5 ? 5 : ytb.rating;
                    }
                }
            }
        }
        return ytb;
    });

    return newList;

}

/**
 *    
 * **/
export const onEditorWorkInSleep = (currentTime, sleepHour, list, editorList) => {

    let ytbList = JSON.parse(JSON.stringify(list));
    for (let i = 0; i < sleepHour; i++) {
        ytbList = onChangeEditorProgress(currentTime, ytbList, editorList);
    }

    return ytbList
}

/**
 *    
 * **/
export const onViewerCountChange = (currentViewer, averageViewer, minute, myTag, contents) => {

    //     

    if (averageViewer) {
        if (currentViewer === 0) { //   30   1  1 
            currentViewer = averageViewer * 0.3 > 1 ? averageViewer * 0.3 : 1
        }
        if (currentViewer <= averageViewer) {
            //   10 
            //  60  30   20
            //  5  2
            const percentage = Math.random() * 100;
            if (percentage < 61) {
                currentViewer = currentViewer * 1.1
            } else if (percentage < 81) {
                currentViewer = currentViewer * 0.95
                currentViewer = currentViewer < 1 ? 1 : currentViewer;
            }

        } else {
            //    15  70   15%
            // 3%  3.5    
            const percentage = Math.random() * 100;
            if (percentage < 16 * (calcTagCoefficient(myTag, contents))) {
                currentViewer = currentViewer * 1.03
            } else if (percentage < 55) {
                currentViewer = currentViewer * 0.96
            }
        }

    } else {
        // 
        //    
        averageViewer = 1;

    }

    if (minute % 120 === 0) {
        //    
        averageViewer = (averageViewer + currentViewer) / 2
    }

    return { currentViewer, averageViewer }
}

/**
 *   
 * @output 1  
 * **/
export const calcPhysiCoefficient = (physicValue) => {

    let physiCoefficient = 1;
    if (physicValue > 9) {
        physiCoefficient = 100 - physicValue * 3.5
    } else if (physicValue > 7) {
        physiCoefficient = 100 - physicValue * 3.2
    } else if (physicValue > 4) {
        physiCoefficient = 100 - physicValue * 2.8
    } else if (physicValue > 2) {
        physiCoefficient = 100 - physicValue * 2
    } else {
        physiCoefficient = 100 - physicValue
    }
    return physiCoefficient;
}

/**
 *   
 * @param  
 * **/
export const getDonationMoney = (currentViewer) => {
    const donationPercent = Math.random();// 
    if (currentViewer > 2000) {
        if (donationPercent < 0.2) return 0;
    } else if (currentViewer > 700) {
        if (donationPercent < 0.4) return 0;
    } else if (currentViewer > 100) {
        if (donationPercent < 0.6) return 0;
    } else {
        if (donationPercent < 0.7) return 0;
    }
    let _100001 = 0.03;
    let _50001 = 0.05;
    let _50000 = 0.05;
    let _30000 = 0.1;
    let _10000 = 0.15;
    let _5000 = 0.25;
    let _1000 = 0.20;

    const _makeMoney = ([a, b, c, d, e, f, g]) => {
        let _returnMoney = 0;
        const _percent = Math.random();

        if (_percent < a) {
            _returnMoney = Math.floor(Math.random() * 100) * 10000
        } else if (_percent < a + b) {
            _returnMoney = Math.floor(Math.random() * 10) * 10000 + 50000
        } else if (_percent < a + b + c) {
            _returnMoney = 50000;
        } else if (_percent < a + b + c + d) {
            _returnMoney = 30000;
        } else if (_percent < a + b + c + d + e) {
            _returnMoney = 10000;
        } else if (_percent < a + b + c + d + e + f) {
            _returnMoney = 5000;
        } else if (_percent < a + b + c + d + e + f + g) {
            _returnMoney = 1000;
        } else {
            _returnMoney = 0;
        }
        return _returnMoney;
    }

    if (currentViewer > 2000) {
        _100001 = 0.03;
        _50001 = 0.01;
        _50000 = 0.10;
        _30000 = 0.10;
        _10000 = 0.15;
        _5000 = 0.30;
        _1000 = 0.10;

    } else if (currentViewer > 700) {
        _100001 = 0.01;
        _50001 = 0.07;
        _50000 = 0.10;
        _30000 = 0.15;
        _10000 = 0.22;
        _5000 = 0.25;
        _1000 = 0.15;
    } else if (currentViewer > 100) {
        _100001 = 0.007;
        _50001 = 0.035;
        _50000 = 0.08;
        _30000 = 0.09;
        _10000 = 0.18;
        _5000 = 0.15;
        _1000 = 0.20;
    } else {
        _100001 = 0.005;
        _50001 = 0.02;
        _50000 = 0.03;
        _30000 = 0.08;
        _10000 = 0.10;
        _5000 = 0.20;
        _1000 = 0.30;
    }
    return _makeMoney([_100001, _50001, _50000, _30000, _10000, _5000, _1000])
}
export const calcDateCoefficient = (currentTime) => {
    /**
     *   
     * 
     *   2 ~ 8   0.6
     *   8 ~ 13   0.65
     *   13 ~ 17   0.7
     *   17 ~ 21   1.2
     *   21 ~ 02   1.4
     * 
     *   2 ~ 8   0.8
     *   8 ~ 13   1.2
     *   13 ~ 17   1.5
     *   17 ~ 21   0.75
     *   21 ~ 02   1.7
     * **/

    let result = 1;

    const _date = moment(currentTime).day();
    const _hour = moment(currentTime).hour();
    if (_date === 6 || _date === 0) { //
        if (_hour > 2 && _hour <= 8) {
            result *= 0.8;
        } else if (_hour > 8 && _hour <= 13) {
            result *= 1.8;
        } else if (_hour > 13 && _hour <= 17) {
            result *= 2;
        } else if (_hour > 17 && _hour <= 21) {
            result *= 0.75;
        } else if (_hour > 21 || _hour <= 2) {
            result *= 1.7;
        }
    } else { //
        if (_hour > 2 && _hour <= 8) {
            result *= 0.6;
        } else if (_hour > 8 && _hour <= 13) {
            result *= 0.65;
        } else if (_hour > 13 && _hour <= 17) {
            result *= 0.7;
        } else if (_hour > 17 && _hour <= 21) {
            result *= 1.2;
        } else if (_hour > 21 && _hour <= 2) {
            result *= 1.4;
        }
    }

    return result;
}

/**
 *    
 * @param1  
 * @return 1.xxxxx ( )
 * **/
export const calcNewsTag = (newsList) => {
    //  ,   
    const newsTag = new Object;
    newsList.map(news => {
        let _keys = Object.keys(news.effect);
        _keys.map(key => {
            newsTag[key] = newsTag[key] ? newsTag[key] : 1;
            newsTag[key] = newsTag[key] * (1 + (news.effect[key] / 100))

        })
    });

    return newsTag;
}


export const calcItemBoost=(inventory,category)=>{
    const summitlistBoost = (list)=>{
        let result = list.reduce((a,affect) => {
            if (affect.name === category) {
                return a + affect.value;
            }else{
                return a
            }
        },0)
        return result;
    }
    // 
    let applyValue = 0;
    if(Object.keys(inventory).length > 0){
        applyValue = Object.keys(inventory).reduce((a, key) => {
            if (inventory[key].use) {
                return a + summitlistBoost(inventory[key].buff.affect); //
            } else if(inventory[key].sideEffect){
                return a + summitlistBoost(inventory[key].debuff.affect); //
            }else{
                return a;
            }
        }, 0)
    }
    return applyValue;
}


export const calcPassiveItemBoost = (inventory,category)=>{

    const summitlistBoost = (list)=>{
        let _result = list.filter(p=>p.name === category);
        return _result;
    }

    // 
    let _applyValue = [];
    if (Object.keys(inventory).length > 0) {
        _applyValue = Object.keys(inventory).map((key) => {
            if (inventory[key].use) {
                return summitlistBoost(inventory[key].buff.passive);;
            } else if(inventory[key].sideEffect){
                return summitlistBoost(inventory[key].debuff.passive)
            }else{
                return [];
            }
        })
            
    }
    //
    let _flatArray = _applyValue.length > 0 ? _applyValue.flat() : [];
    let resultList = _applyValue.length > 0 ? _applyValue.flat() : [];

    //     
    if(category === 'stress'){
        let intervalList = [];
        if(_flatArray.length > 0){
            let preInterval={};
            _flatArray.map(item=>{
                if(item.interval){
                    if(preInterval.hour !== item.interval.hour && preInterval.minute !== item.interval.minute){
                        intervalList.push(item.interval);
                        preInterval = item.interval
                    }
                }
            })
        }
        resultList = [];
        intervalList.map(interval=>{
            const _filterd = _flatArray.filter(f=>f.interval.hour === interval.hour && f.interval.minute === interval.minute);
            const _timeSummit = new Object();
            _timeSummit.interval = interval.minute+(interval.hour*60);
            
            _timeSummit.value = _filterd.reduce((a,f)=>{
                return a + f.value
            },0)
            resultList.push(_timeSummit);
        })
    
    }
    return resultList;
}