import moment from 'moment';
import React, { useRef, useState } from 'react'
import { useDispatch } from 'react-redux';
import { setCurrentTime } from '../../_actions/currentTime_action';
import honjul from './img/honjul.jpg'
import crying from './img/crying.jpg'
import crying1 from './img/crying1.jpg'
import crying2 from './img/crying2.png'
import crying3 from './img/crying3.jpeg'

import openingMusic from '../../audio/opening_music.mp3';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { setSetting } from '../../_actions/setting_action';

function IntroComponent(props) {

    const dispatch = useDispatch();
    const introMusicRef = useRef();
    const introText = useRef(
        [
            ' :  !  ',
            ' : ..?...   ..',
            '   ....',
            ` : '..     ..  '`,
            <>....<br /><br />    .<br /><br /><br /><p className={'small'}>(     )</p></>,
            <>'...  '<br />'    ..'</>,
            <>'      ?'</>,
            <>'    ..'</>,
            <>  : " .. ??"</>,
            <> : "? ?"</>,
            <> <br /><br />    .  !  </>,
            <><span className={'big sigong'}>'   !' </span> <br />  3 . <br />   .</>,
            <> :    ..     ?</>,
            <>  <br /><br />~  ?</>,
            <><img src={crying} className={'crying'} /><span className={'small'}>......</span></>,
            <> <br /><br /><span className={'big'}> !</span></>,
            <><img src={crying1} className={'crying1'} /><span className={'big sigong'}>...   !</span></>,
            <><img src={crying2} className={'crying2'} /><span className={'big sigong'}>   !</span></>,
            <><img src={crying3} className={'crying3'} /><span className={'big sigong'}>  .. !</span></>,
            <><img src={honjul} className={'honjul'} /></>,
            <>! !</>,
            <>... ?....<br /><br /><br />( )</>,
            <> : <br /> &nbsp;&nbsp;&nbsp; .<br /> &nbsp;&nbsp;&nbsp;    <br /> &nbsp;&nbsp;&nbsp;  .<br /> &nbsp;&nbsp;&nbsp; !! <br /></>,
            <>????  ??? ...<br /><br />...<br /><br /><span className={'small'}>(   )</span></>,
            <>    ...<br />      ...<br />..</>,
            <><span className={'big'}> .</span></>
        ]
    )
    const [textIndex, setTextIndex] = useState(0);
    const {Music:{on:MusicOn,volume:MusicVolume}} = useSelector(state => state.setting);

    useEffect(()=>{
        const setting = JSON.parse(window.localStorage.getItem('setting'));
        dispatch(setSetting(setting))
    },[])

    useEffect(() => {

        if(textIndex === 1){
            introMusicRef.current.volume = MusicVolume/100;
            if(MusicOn) introMusicRef.current.play();
        }
    }, [textIndex])

    const onNextText = () => {
        setTextIndex(textIndex + 1);
    }

    const onStartGame = () => {
        dispatch(setCurrentTime(moment().set('second', 0).valueOf()))
        props.history.push('/client/status')
    }

    return (
        <div className={'intro'}>
            <div className={'text_container'}>
                {
                    introText.current[textIndex]
                }
            </div>
            {introText.current.length - 1 === textIndex ?
                <div className="btn_wrapper">
                    <div className={'check_btn'} onClick={onStartGame}>
                         
                    </div>
                </div>
                :
                <div className='btn_wrapper'> 
                    <div className={'jump_btn'} onClick={onStartGame}>
                        {'>>>'}
                    </div>
                    <div className={'check_btn'} onClick={onNextText}>
                        {'>'}
                    </div>
                </div>
            }

            <audio ref={introMusicRef} src={openingMusic} style={{display:'none'}} loop/>
        </div>
    )
}

export default IntroComponent
