import moment from 'moment';
import React, { memo, useEffect, useRef, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import CustomButton from '../../../../../../UI/Button/CustomButton';
import { infoMessage } from '../../../../../../UI/Message/message';
import CustomModal from '../../../../../../UI/Modal/CustomModal'
import { AddComma, momentTimeString, momentToDateString } from '../../../../../../util/commonMethod';
import { sellCoin } from '../../../../../../_actions/asset_action';
import { getMoney } from '../../../../../../_actions/character_action';

const CoinSellModal = memo((props) => {

    const { selectedCoin } = props;
    const { coinList } = useSelector(state => state.coin);
    const currentTime = useSelector(state => state.currentTime);
    const [sellCount, setSellCount] = useState(0); //
    const [currentCoin, setCurrentCoin] = useState({}); //  (   )

    const dispatch = useDispatch();

    useEffect(() => {
        const coin = coinList.find(coin => coin.key === selectedCoin.key)
        setCurrentCoin(coin);

    }, [coinList, selectedCoin])

    const onSellCoin = () => {
        if(sellCount < 1) return infoMessage(' ');
        if (sellCount > selectedCoin.totalCount) return infoMessage('   .')
        const body = {
            ...selectedCoin,
            totalCount: selectedCoin.totalCount - sellCount,
        }
        dispatch(sellCoin(body))
        dispatch(getMoney(sellCount * currentCoin.price));
        onCloseModal();
    }
    const onInputSellAll = () => {
        setSellCount(selectedCoin.totalCount);
    }

    const onCloseModal = () => {
        setSellCount(0)
        props.ModalHandler(false);
    }

    return (
        <CustomModal
            {...props}
            title={' '}
            className={'coin_sell_panel'}
        >
            <div className={'coin_sell_body'}>
                <div className={'title hc'}>
                     
                </div>
                <div>
                    {momentToDateString(currentTime)} {momentTimeString(currentTime)}
                </div>
                <div className={'title hc'}>
                    
                </div>
                <div>
                    {AddComma(currentCoin?.price)} 
                </div>
                <div>
                    
                </div>
                <div>
                    {AddComma(selectedCoin.totalCount)} 
                </div>
                <div className={'title hc'}>
                     
                </div>
                <div className={'input hc vc'}>
                    <div className={'sellAll'} onClick={onInputSellAll}></div>
                    <input type={'number'} min={0} value={(sellCount).toString()} onChange={e => setSellCount(Number(e.currentTarget.value))} /> 
                </div>

                <div className={'button hc'}>
                    <CustomButton onClick={onSellCoin}>
                        
                    </CustomButton>
                    <CustomButton type={'danger'} onClick={onCloseModal}>
                        
                    </CustomButton>
                </div>
            </div>

        </CustomModal>
    )
})

export default CoinSellModal
