import moment from 'moment';
import React, { memo } from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import CustomModal from '../../..//UI/Modal/CustomModal'
import CustomButton from '../../../UI/Button/CustomButton';
import { confirmMessage, infoMessage } from '../../../UI/Message/message';
import { calcComputerScore, TimeComparison } from '../../../util/commonMethod';
import { setStatus } from '../../../_actions/character_action';
import { setPassiveItem, setUseInventoryItem } from '../../../_actions/inventory_action';
import { shootingInfomationMsg } from '../../Main/Section/shootingToast';
import ModalUnfoldPanel from '../options/ModalUnfoldPanel';
import { expandable_product } from '../../../Assets/shopAsset';
import EquipmentModal from './Equipment/EquipmentModal';
import CosmeticSurgeryModal from './Section/CosmeticSurgeryModal';
import RandomHealthModal from './Section/RandomHealthModal';
import RandomPhysicalModal from './Section/RandomPhysicalModal';
import RandomSpeachModal from './Section/RandomSpeachModal';
import RandomVoiceModal from './Section/RandomVoiceModal';

const InventoryModal = memo((props) => {


    const [equipmentModalOpen, setequipmentModalOpen] = useState(false);
    const [TotalEquipScore, setTotalEquipScore] = useState(0);
    const computer = useSelector(state => state.computer)
    const inventory = useSelector(state => state.inventory);
    const { health } = useSelector(state => state.character);
    const currentTime = useSelector(state => state.currentTime);


    const dispatch = useDispatch()
    //  
    const [cosmeticSurgeryModalOpen, setCosmeticSurgeryModalOpen] = useState(false);
    //  
    const [randomHealthModalOpen, setRandomHealthModalOpen] = useState(false);
    //
    const [randomPhysicalModal, setRandomPhysicalModal] = useState(false);
    //
    const [randomSpeechModalOpen, setRandomSpeechModalOpen] = useState(false);
    //
    const [randomVoiceModalOpen, setRandomVoiceModalOpen] = useState(false);
    useEffect(async () => {
        if (Object.keys(computer).length > 0) {
            const computerScore = calcComputerScore(computer);
            setTotalEquipScore(computerScore.toFixed(2));
        }
    }, [computer])
    const calcBuffTime = (item) => {
        const { buff, debuff } = item;
        let buffExpireTime = null;
        let debuffExpireTime = null;
        if(buff){
            const {duration} = buff
            buffExpireTime = moment(currentTime).add(duration.hour, 'hour').add(duration.minute, 'minute').valueOf();
        }
        if(debuff){
            const {duration} = debuff;
            debuffExpireTime = moment(buffExpireTime).add(duration.hour, 'hour').add(duration.minute, 'minute').valueOf();
        }

        return { buffExpireTime, debuffExpireTime }
    }

    const onUseItem = (item) => {
        // 
        switch (item.key) {

            case 'ex_1':
                // M
                //  30 
                //5 
                if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg(' .', 'red');
                    const { buff: { duration } } = item;
                    const expireTime = moment(currentTime).add(duration.hour, 'hour').add(duration.minute, 'minute').valueOf();
                    dispatch(setPassiveItem(item.key, expireTime))
                }
                break;
            case 'ex_2':
                //SJ-1
                /**
                 *   8  50 
                 *  5   25
                 * **/
                if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg('SJ-1 .', 'red');
                    const time = calcBuffTime(item);
                    dispatch(setPassiveItem(item.key, time.buffExpireTime, time.debuffExpireTime))
                }
                break;
            case 'ex_3':
                // SJ-6
                /**
                 * 10 100
                 *  2  
                 * **/
                if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg('SJ-6 .', 'red');
                    const time = calcBuffTime(item);
                    dispatch(setPassiveItem(item.key, time.buffExpireTime, time.debuffExpireTime))
                }
                break;
            case 'ex_4':
                //
                /**
                 *  5 30 2 
                 * **/
                if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg(' .', 'red');
                    const time = calcBuffTime(item);
                    dispatch(setPassiveItem(item.key, time.buffExpireTime, time.debuffExpireTime))
                }
                break;
            case 'ex_5':
                /**
                 *  
                 * **/
                confirmMessage(`  1 . ?`, () => {
                    let upHealth = health.value + 1;
                    if (upHealth > 10) {
                        return infoMessage(' 10 .');
                    } else {
                        dispatch(setStatus('health', upHealth))
                        dispatch(setUseInventoryItem('ex_5'))
                    }
                })
                break;
            case 'ex_6':
                /**
                 * 
                 * **/
                 if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg(' .', 'red');
                    const time = calcBuffTime(item);
                    dispatch(setPassiveItem(item.key, time.buffExpireTime, time.debuffExpireTime))
                }
                break;
            case 'ex_7':
                /**
                 *  
                 * **/
                 if (inventory[item.key].count > 0 && !inventory[item.key].use) {
                    shootingInfomationMsg(' . !', 'red');
                    const time = calcBuffTime(item);
                    dispatch(setPassiveItem(item.key, time.buffExpireTime, time.debuffExpireTime))
                }
                

                break;
            case 'ex_8':
                /**
                 * 
                 * **/
                setCosmeticSurgeryModalOpen(true);
                break;

            case 'ex_9':
                /**
                 *  
                 * **/
                setRandomVoiceModalOpen(true);
                break;

            case 'ex_10':
                /*
                 
                */
                setRandomSpeechModalOpen(true);
                break;

            case 'ex_11':
                /**
                 * N  
                 * **/
                setRandomPhysicalModal(true);
                break;

            case 'ex_12':
                /**
                 *  
                 * **/
                setRandomHealthModalOpen(true);
                break;

            default: infoMessage(` .`); break;
        }
    }

    return (
        <CustomModal
            {...props}
            title={'  '}
            className={'inventory_modal'}
        >
            <ModalUnfoldPanel text={<> </>} toggle unFoldStyle={{ height: '25%' }}
                value={<CustomButton onClick={() => setequipmentModalOpen(true)}></CustomButton>}
            >
                   : {TotalEquipScore}
            </ModalUnfoldPanel>

            {Object.keys(inventory).map(inven => {
                const item = inventory[inven];
                const { desc } = expandable_product.find(e => e.key === item.key);
                let displayTime = '00:00:00';
                if (item.use) displayTime = TimeComparison(item.buffExpireTime, currentTime);
                if (item.sideEffect) displayTime = TimeComparison(item.debuffExpireTime, currentTime);
                return (
                    <ModalUnfoldPanel key={item.key}
                        unFoldStyle={{ height: '35%' }}
                        toggle
                        text={<>{item.name} : {item.count}</>}
                        textSize={'small'}
                        value={
                            item.use ?
                                <CustomButton type={'confirm'}>{displayTime}</CustomButton>
                                :
                                item.sideEffect ?
                                    <CustomButton type={'danger'}>{displayTime}</CustomButton>
                                    :
                                    <CustomButton onClick={() => onUseItem(item)}></CustomButton>
                        }
                    >
                        -<br />
                        {desc}
                    </ModalUnfoldPanel>
                )
            })}
            <EquipmentModal visible={equipmentModalOpen} ModalHandler={setequipmentModalOpen} />
            <CosmeticSurgeryModal visible={cosmeticSurgeryModalOpen} ModalHandler={setCosmeticSurgeryModalOpen} toggle={false} />
            <RandomHealthModal visible={randomHealthModalOpen} ModalHandler={setRandomHealthModalOpen} toggle={false} />
            <RandomPhysicalModal visible={randomPhysicalModal} ModalHandler={setRandomPhysicalModal} toggle={false} />
            <RandomSpeachModal visible={randomSpeechModalOpen} ModalHandler={setRandomSpeechModalOpen} toggle={false} />
            <RandomVoiceModal visible={randomVoiceModalOpen} ModalHandler={setRandomVoiceModalOpen} toggle={false} />
        </CustomModal>
    );
})

export default InventoryModal;
