import React, { memo, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CustomButton from '../../../../UI/Button/CustomButton'
import { confirmMessage, infoMessage } from '../../../../UI/Message/message'
import CustomModal from '../../../../UI/Modal/CustomModal'
import { AddComma, sortingForObjectList, TimeComparison } from '../../../../util/commonMethod'
import { payMoney } from '../../../../_actions/character_action'
import { addEditor } from '../../../../_actions/editor_action'
import { setNowTime } from '../../../../_actions/new_editor_action'
import { useInterval } from '../../../Main/Section/useInterval'
import { employeeList } from './editorList'


const EditorEmployeModal = memo((props) => {
    const editor = useSelector(state => state.editorList);
    const { money: { value: myMoney } } = useSelector(state => state.character)
    const dispatch = useDispatch();
    const [editorAbil, setEditorAbil] = useState(false); //false :   || true : 
    const [editorCost, setEditorCost] = useState(false);
    const [multiEdit, setMultiEdit] = useState(false);
    const [eidtorList, setEidtorList] = useState([])
    const [searchTerm, setSearchTerm] = useState('')

    const [recallTimer, setRecallTimer] = useState('');

    useEffect(() => {
        setEidtorList(employeeList);
    }, [])

    // useInterval(() => {
    //     dispatch(setNowTime(moment().valueOf()));
    //     setRecallTimer(TimeComparison(newEditor.recall, newEditor.now))
    // }, 1000, true)


    const editorAbilSort = () => {
        setEditorAbil(!editorAbil);
        const newList = sortingForObjectList(eidtorList, !editorAbil, 'edit_ability');
        setEidtorList(newList)
    }

    const editorCostSort = () => {
        setEditorCost(!editorCost);
        const newList = sortingForObjectList(eidtorList, !editorCost, 'cost');
        setEidtorList(newList)
    }
    const multiEditSort = () => {
        setMultiEdit(!multiEdit);
        const newList = sortingForObjectList(eidtorList, !multiEdit, 'work_count');
        setEidtorList(newList)
    }

    const onAddEditor = (editor) => {
        confirmMessage(`$${editor.cost * 8} <br/>"${editor.name}"<br/>?`, () => {
            if (myMoney < editor.cost * 8) {
                return infoMessage(' .')
            } else {
                dispatch(payMoney(editor.cost * 8));
                dispatch(addEditor(editor));
            }
        })
    }
    const onSearchTermChange = (e)=>{
        const text= e.currentTarget.value.toUpperCase()
        setSearchTerm(text);
        
        const newList = eidtorList.filter(item=>{
            let flag = item.tag.filter(t=>t.indexOf(text) > -1 ).length;
            if(flag > 0){
                return item;
            }
        })
        setEidtorList(newList);
    }

    return (
        <CustomModal
            {...props}
            title=" "
            className={'editor_modal'}
        >
            <div className={'editor_add_body'}>
                {/* <div className={'recall_container'}>
                    <div> {recallTimer}  </div>
                    <div className="recall" onClick={editorCostSort}>   </div>
                </div> */}
                <div class={'search_bar'}>
                    <div onClick={editorAbilSort}> {editorAbil ? '' : ''} </div>
                    <div onClick={editorCostSort}> {editorCost ? '' : ''} </div>
                    <div onClick={multiEditSort}> {multiEdit ? '' : ''} </div>
                    <input placeholder={' '} style={{ width: '20vw' }} value={searchTerm} onChange={onSearchTermChange}/>
                </div>
                <div className={'editor_list'}>
                    {eidtorList.map((item, idx) => {
                        const { edit_ability, work_count, cost, name, tag, working_time,desc } = item;
                        return (
                            <div key={idx} className="editor_container">
                                <div>
                                     : {name}
                                </div>
                                <div className={'abil_grid'}>
                                    <div>  : {edit_ability} </div>
                                    <div>  : {AddComma(cost)} </div>
                                </div>
                                <div>     : {work_count} </div>
                                <div>   : {working_time[0]} ~ {working_time[1]}</div>
                                <div>   : {desc}</div>
                                <div>   </div>
                                <div className="tag_container">
                                    {tag.map(item => {
                                        return <span className="tag" key={item}>{item}</span>
                                    })}
                                </div>
                                <div className='contract_button'>
                                    {
                                        editor.find(emp => emp.key === item.key) ?
                                            <CustomButton >
                                                 
                                            </CustomButton>
                                            :
                                            <CustomButton onClick={() => onAddEditor(item)}>
                                                 ${AddComma(cost * 8)}
                                            </CustomButton>
                                    }
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>

        </CustomModal>
    )
})

export default EditorEmployeModal
