import React, { useEffect, useRef } from 'react'
import { useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { confirmMessage, infoMessage } from '../../../UI/Message/message';
import { setTutorial } from '../../../_actions/tutorial_action';

function TutorialComponent(props) {

    const tutorial = useSelector(state => state.tutorial);
    const dispatch = useDispatch();

    const [tutorialQuestion, setTutorialQuestion] = useState(JSON.parse(window.localStorage.getItem('tutorialShow')) === null ? true : JSON.parse(window.localStorage.getItem('tutorialShow')));
    const [tutorialIndex, setTutorialIndex] = useState(0);
    // const tutorialProgress = JSON.parse(window.localStorage.getItem('tutorial'));
    const prevStyle = useRef();
    const tutorialScript = useRef([
        {
            id: 'char_monitor',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                      .      .    ''  .<br /><br />
                      <span className='blue'>D-Day</span>    .<br />

                      <span className='blue'></span>    .      .<br /><br />
                      <span className='blue'> </span>    .<br />

                </div>,
            textPosition: 50
        },
        {
            id: 'stress_bar',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                      .
                      <span className="red">80%</span>   <span className='red'></span>  .
                    <br />
                    80%   .         .
                </div>,
            textPosition: 60
        },
        {
            id: 'Timer_box',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                       .<br />
                    <span className="blue"> </span>    <span className="red"> / </span>   .<br />
                    <span className="blue">  </span>    <span className="red"> / </span>    .<br />

                </div>,
            textPosition: 65
        }, {
            id: 'control_1',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                     .
                 0    ,     ,   .
            </div>,
            textPosition: 20
        }, {
            id: 'control_2',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                  .<br />
                <span className="blue"></span> :  .<br />
                -     .<br />
                -       .<br />
                -  . <br />
                <br />
                <span className="blue"></span> :   .<br />
                -        .<br />
                -  . <br />
                <br />
                <span className="blue"></span>: .    .<br />
                <span className="blue"></span> :    . <br />
                -      .<br />
                <br />
                <span className="blue"></span> : 6~9  .   .<br />
                <br />
                <span className="blue"></span> :  .<br />
                -       .<br />
                -  . <br />
            </div>,
            textPosition: 15
        }, {
            id: 'control_3',
            zIndex: 1,
            info: <>
                <span className="blue"> </span>   .<br />
                  <span className="red"> </span>   ,    ,    .<br />
            </>,
            textPosition: 30
        },
        {
            id: 'control_4',
            zIndex: 1,
            info:
                <>
                       <span className="blue"> </span>    .<br />
                           .<br/>
                     <span className="red"></span> .<br />
                </>,
            textPosition: 30
        }, {
            id: 'control_5',
            zIndex: 1,
            info:
                <>
                       <span className="blue"> </span>.<br />
                       <span className="red"> </span>   .<br />
                       <span className='red'> </span>  .<br />
                       <span className="red"></span>    <span className="red">   </span> .
                </>,
            textPosition: 20
        }, {
            id: 'control_6',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                   .<br /><br />
                1.   <span className='blue'>  0</span> <span className="red"></span>.<br />
                2.     <span className="blue">  </span>   .<br />
                3.     <span className='blue'> </span>    <span className="red"></span>.<br />
                4.      <span className="blue"></span>.
                5.  <span className='red'></span> .<br />
            </div>,
            textPosition: 30
        }, {
            id: 'control_7',
            zIndex: 1,
            info: <>
                     . <br /><br />
                <span className="blue"></span> <span className="red"></span>    .<br /><br />
                 <span className="blue"> </span> .
            </>,
            textPosition: 30
        },  {
            id: 'control_8',
            zIndex: 1,
            info: <>
                  . <br />
                 .<br />
                
            </>,
            textPosition: 30
        },{
            id: 'control_9',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                   .<br /><br />
                <span className="blue"></span> <span className="red">/</span>   .<br />
                <span className="red"> </span>     .
                <br /><br />
                <span className="blue"></span> <span className="red"></span>    .<br />
                <span className="red"> 3 </span>   .<br />
                <span className="red"></span>      <span className="red"></span> .
                <br /><br />
                tip .    .   
                 <span className="blue"> </span> .
            </div>,
            textPosition: 20
        },
        {
            id: 'control_10',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                  :  .<br />
                        .<br />
                     . :)<br />
                <br />
                  :    .<br />
                     <br />
                 .<br />
                <br />
                {"&"} :     .<br />
                <br />
                <br />
                -  <br />
                 : <span className="blue">, </span> <br />
                 : <span className="blue"></span> <br />
                 : <span className="blue"> </span> <br />
                 : <span className="blue"> </span> ()<br />
                 : <span className="blue">, </span> ()<br />
                 : ~<br />

                <br />
                tip.     ...<br />
                   .
                <br />
                -  <br />
                 : <span className="blue"></span> <span className="red"></span>.<br />
                CPU :  .<br />
                GPU :        .<br />
                RAM :        .<br />

                      .<br />
                <br />

                
            </div>,
            textPosition: 10
        },
        {
            id: 'control_11',
            zIndex: 1,
            info: <>
                 .<br />      .
            </>,
            textPosition: 30
        }, {
            id: 'control_12',
            zIndex: 1,
            info: <>
                .<br />
                     .
            </>,
            textPosition: 30
        }, {
            id: '',
            info:
                <div style={{ width: '100%', height: '80vh', overflowY: 'auto' }}>
                     
                    <br />
                    <br />
                    <span className="blue">1</span>.   <br />
                    <span className="blue">2</span>.   <br />
                    <span className="blue">3</span>.  <br />
                    <span className="blue">4</span>.   <br />
                    <span className="blue">5</span>. <br />
                    <span className="red">6</span>. 
                    <br />
                    <br />
                    <span className="red"></span>:  1   0
                    <br />
                    <br />
                      " " .<br />
                         ""       .
                    <br />
                    <br />
                     ,   ,       .<br />
                          ,  .<br />
                    <span style={{ textDecoration: 'line-through' }}>(   ? )</span><br />
                         .<br />
                        .<br />
                </div>,
            textPosition: 10

        }

    ])

    useEffect(() => {

        if (tutorialQuestion) {
            confirmMessage(`  ?`, () => {
                dispatch(setTutorial(true));
            }, () => {
                window.localStorage.setItem('tutorialShow', JSON.stringify(false));
                dispatch(setTutorial(false));
                setTutorialQuestion(false);
                infoMessage(`           .`)
            })
        }
    }, [tutorialQuestion])
    useEffect(() => {
        if (tutorial) {
            setTutorialQuestion(false);
            const targetElement = document.getElementById(tutorialScript.current[tutorialIndex].id);
            getPrevStyle(targetElement);
            targetElement.style.zIndex = tutorialScript.current[tutorialIndex].zIndex
        }

    }, [tutorial])


    const onJumpTutorial = () => {
        confirmMessage(`   ?`, () => {
            window.localStorage.setItem('tutorialShow', JSON.stringify(false))
            setPrevStyle(document.getElementById(tutorialScript.current[tutorialIndex].id))
            dispatch(setTutorial(false))
            setTutorialIndex(0);
            infoMessage('  ,         .')
        })
    }
    const onNextGuide = () => {
        //  
        const prevEle = document.getElementById(tutorialScript.current[tutorialIndex].id);
        if (prevEle) {
            setPrevStyle(prevEle)
        }

        if (tutorialIndex + 1 >= tutorialScript.current.length) {
            infoMessage('   . .', () => {
                setTutorialIndex(0);
                dispatch(setTutorial(false))
                window.localStorage.setItem('tutorialShow', JSON.stringify(false));
            })
        } else {
            // 
            setTutorialIndex(tutorialIndex + 1)
            //   
            const elementContents = tutorialScript.current[tutorialIndex + 1];
            if (elementContents.id) {
                const targetElement = document.getElementById(elementContents.id);
                getPrevStyle(targetElement);
                targetElement.style.zIndex = elementContents.zIndex;
                if (!targetElement.style.position || targetElement.style.position === 'static') targetElement.style.position = 'relative'
            }
        }
    }
    const getPrevStyle = (ele) => {

        const computeStyle = window.getComputedStyle(ele);
        prevStyle.current = {
            zIndex: computeStyle.getPropertyValue('z-index') || 0,
            position: computeStyle.getPropertyValue('position') || 'unset'
        }
    }
    const setPrevStyle = (ele) => {
        if(ele){
            ele.style.position = prevStyle.current.position;
            ele.style.zIndex = prevStyle.current.zIndex;
        }
    }


    if (tutorial) {
        return (
            <div className='tutotial_wrapper'>
                <div className={`tutorial_text `}
                    style={{ top: `${tutorialScript.current[tutorialIndex].textPosition}%` }}
                >
                    {tutorialScript.current[tutorialIndex].info}
                </div>
                <div className="control_btn" >
                    <div className='jump' onClick={onJumpTutorial}>
                        
                    </div>
                    <div className="next" onClick={onNextGuide}>
                        {">>>"}
                    </div>
                </div>

            </div>
        )
    } else {
        return <></>
    }

}

export default TutorialComponent